import React from 'react'; 
import './style.css'; 


const checkCompanyName = (companyName) => {
    switch(companyName) {
        case `InvisionCore`: 
            return <span className="logo1"><i className="fa fa-globe"></i></span>
        case `Stratum Optics`:
            return <span className="logo2"><i className="fa fa-sitemap"></i></span>
        case `Cyberron`:
            return <span className="logo3"><i className="fa fa-handshake-o"></i></span>   
        default:
            return<span>Nothing!</span>
    }
}


const Card = (props) => {
    return (
        <React.Fragment>
            <div className="card">
                <div className="cardTitle">
                    {props.companyName}
                </div>
                <div className="cardImg">
                     {
                      checkCompanyName(props.companyName)
                     }
                </div>
                <button className="button cprimary">{props.companyDescription}</button>
            </div>
        </React.Fragment>
    )
}

export default Card; 

