import React, {Component} from "react"; 
import { HashRouter, Route, Link, Switch} from "react-router-dom"; 

import HomePage from '../components/HomePage';

class App extends Component {
    constructor() {
        super();
        this.state ={}
    }
    render() {
        return (
            <HashRouter>
                <Switch>
                    <Route 
                    exact 
                    path="/"
                    component={HomePage}
                    />
                </Switch>
            </HashRouter>
        )
    }
}

export default App;
