import React, {Component} from 'react'; 
import './style.css'; 


class Banner extends Component {
    constructor(props) {
        super(props); 
        this.state = {
            text: [
                {
                    id: 1, 
                    text: `Stop. Think. Innovate!`,
                }, 
                {
                    id: 2, 
                    text: 'Helping the World, One day at a time'
                }, 
                {
                    id: 3, 
                    text:  `Never Give Up, Keep Going!`
                }
            ],
            newText: `Stop. Think. Innovate!`,
        }
    }

    componentDidMount() {
        this.changeText(this.state.text)
    }

    changeText = (text) => {
        setInterval(() => {
        const randomId = Math.floor(Math.random() * text.length); 
        this.setState({
            newText: text[randomId].text,
        })
        }, 5000)
    }

    render() {
        return(
            <React.Fragment>
                <div className="banner">
        <span className="bannerText">{this.state.newText}</span>
                </div>
            </React.Fragment>
        )
    }
}

export default Banner; 

